"use client";
import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { FiPlus, FiSearch, FiFilter, FiRefreshCw } from 'react-icons/fi';
import CarTable from './components/CarTable';
import CarForm from './components/CarForm';
import Stats from './components/Stats';
import { fetchCars, createCar, updateCar, deleteCar, searchCars } from './api/carServices';
import { Car } from './types';
import Swal from 'sweetalert2';

export default function ManageCars() {
  const [cars, setCars] = useState<Car[]>([]);
  const [filteredCars, setFilteredCars] = useState<Car[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [isFormOpen, setIsFormOpen] = useState(false);
  const [selectedCar, setSelectedCar] = useState<Car | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  // Load cars on component mount
  useEffect(() => {
    loadCars();
  }, []);

  const loadCars = async () => {
    try {
      setIsLoading(true);
      const { success, data, message } = await fetchCars();
      if (success) {
        setCars(data);
        setFilteredCars(data);
      } else {
        throw new Error(message);
      }
    } catch (error) {
      Swal.fire({
        title: 'Error!',
        text: error instanceof Error ? error.message : 'Failed to load cars',
        icon: 'error',
        showClass: { popup: 'animate__animated animate__shakeX' }
      });
    } finally {
      setIsLoading(false);
    }
  };

  // Handle search functionality
  useEffect(() => {
    const timer = setTimeout(() => {
      if (searchTerm) {
        handleSearch(searchTerm);
      } else {
        setFilteredCars(cars);
      }
    }, 300);

    return () => clearTimeout(timer);
  }, [searchTerm, cars]);

  const handleSearch = async (query: string) => {
    try {
      setIsLoading(true);
      const { success, data, message } = await searchCars(query);
      if (success) {
        setFilteredCars(data);
      } else {
        throw new Error(message);
      }
    } catch (error) {
      Swal.fire({
        title: 'Error!',
        text: error instanceof Error ? error.message : 'Search failed',
        icon: 'error',
        showClass: { popup: 'animate__animated animate__shakeX' }
      });
    } finally {
      setIsLoading(false);
    }
  };

  const handleAddCar = async (newCar: Car) => {
    try {
      const { success, data, message } = await createCar(newCar);
      if (success) {
        setCars(prev => [...prev, data]);
        setFilteredCars(prev => [...prev, data]);
        setIsFormOpen(false);
        Swal.fire({
          title: 'Success!',
          text: 'Car registered successfully',
          icon: 'success',
          showClass: { popup: 'animate__animated animate__bounceIn' }
        });
      } else {
        throw new Error(message);
      }
    } catch (error) {
      Swal.fire({
        title: 'Error!',
        text: error instanceof Error ? error.message : 'Failed to register car',
        icon: 'error',
        showClass: { popup: 'animate__animated animate__shakeX' }
      });
    }
  };

  const handleUpdateCar = async (updatedCar: Car) => {
    try {
      const { success, data, message } = await updateCar(updatedCar.id, updatedCar);
      if (success) {
        setCars(prev => prev.map(car => car.id === data.id ? data : car));
        setFilteredCars(prev => prev.map(car => car.id === data.id ? data : car));
        setSelectedCar(null);
        Swal.fire({
          title: 'Success!',
          text: 'Car updated successfully',
          icon: 'success',
          showClass: { popup: 'animate__animated animate__bounceIn' }
        });
      } else {
        throw new Error(message);
      }
    } catch (error) {
      Swal.fire({
        title: 'Error!',
        text: error instanceof Error ? error.message : 'Failed to update car',
        icon: 'error',
        showClass: { popup: 'animate__animated animate__shakeX' }
      });
    }
  };

  const handleDeleteCar = async (id: number) => {
    const result = await Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!',
      showClass: { popup: 'animate__animated animate__fadeInDown' },
      hideClass: { popup: 'animate__animated animate__fadeOutUp' }
    });

    if (result.isConfirmed) {
      try {
        const { success, message } = await deleteCar(id);
        if (success) {
          setCars(prev => prev.filter(car => car.id !== id));
          setFilteredCars(prev => prev.filter(car => car.id !== id));
          Swal.fire({
            title: 'Deleted!',
            text: 'Car has been deleted.',
            icon: 'success',
            showClass: { popup: 'animate__animated animate__bounceIn' }
          });
        } else {
          throw new Error(message);
        }
      } catch (error) {
        Swal.fire({
          title: 'Error!',
          text: error instanceof Error ? error.message : 'Failed to delete car',
          icon: 'error',
          showClass: { popup: 'animate__animated animate__shakeX' }
        });
      }
    }
  };

  return (
    <div className="p-6 space-y-6 overflow-x-hidden"> {/* Prevent any horizontal scrolling */}
      {/* Header Section - unchanged */}
      <motion.div
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4"
      >
        {/* ... existing header content ... */}
      </motion.div>
  
      {/* Stats Section - unchanged */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.2, duration: 0.5 }}
      >
        <Stats cars={cars} />
      </motion.div>
  
      {/* Main Content - modified */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.4, duration: 0.5 }}
        className="bg-white rounded-xl shadow-lg"
      >
        {/* Table Header - unchanged */}
        <div className="p-4 border-b border-gray-200 flex justify-between items-center">
          {/* ... existing table header content ... */}
        </div>
  
        {/* Loading State - unchanged */}
        {isLoading ? (
          <div className="p-8 flex justify-center items-center">
            {/* ... existing loading spinner ... */}
          </div>
        ) : (
          /* Table Container - no scrolling */
          <div className="w-full">
            <table className="w-full divide-y divide-gray-200 table-fixed"> {/* Added table-fixed */}
              <CarTable 
                cars={filteredCars} 
                onEdit={(car) => {
                  setSelectedCar(car);
                  setIsFormOpen(true);
                }} 
                onDelete={handleDeleteCar} 
              />
            </table>
          </div>
        )}
      </motion.div>
  
      {/* Car Form Modal - unchanged */}
      <AnimatePresence>
        {/* ... existing modal content ... */}
      </AnimatePresence>
    </div>
  );
}